<template>
  <div style="background:rgb(245, 245, 245);">
    <van-nav-bar
      
      title="任务详情"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="search" slot="right" /> -->
    </van-nav-bar>
    <div class="contanerMain border-radius">
      <!-- <select name="" id=""></select> -->
      <div>
        <div v-for="(item,index) in DataList1" :key="index">
          <div class="eventDetailsDataList">
            <span style="color:#7a7a7a">站点名称</span>
            <span>{{item.station}}</span>
          </div>
          <div class="eventDetailsDataList">
            <span style="color:#7a7a7a">派发时间</span>
            <span>{{item.gmtPublish}}</span>
          </div>
          <div class="eventDetailsDataList">
            <span style="color:#7a7a7a">接收人员</span>
            <span>{{item.recevPerson}}</span>
          </div>
          <div class="eventDetailsDataList">
            <span style="color:#7a7a7a">任务类型</span>
            <span>{{item.type}}</span>
          </div>
          <div class="eventDetailsDataList">
            <span style="color:#7a7a7a">任务状态</span>
            <span>{{item.status}}</span>
          </div>
          <div class="eventDetailsDataList">
            <span style="color:#7a7a7a">接收时间</span>
            <span>{{item.gmtPublish}}</span>
          </div>
          <div class="eventDetailsDataList">
            <span style="color:#7a7a7a">完成时间</span>
            <span>{{item.gmtFinish}}</span>
          </div>

          <!-- <div class="eventDetailsDataList">
            <span style="color:#7a7a7a">派发时间</span>
            <span>{{item.gmtPublish}}</span>
          </div>-->
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
export default {
  name: "maintenanceChild3",
  data() {
    return {
      panel: "",
      token: "",
      DataList: [],
      DataList1: [],
      loading: false
    };
  },
  methods: {
    fullscreen() {
      this.loading = this.$loading();
    },
    toggle(panel) {
      this.panel = panel === this.panel ? "" : panel;
    },
    async initData(e) {
      this.fullscreen();
      var url = "/task";
      var pageNum = 1;
      var pageSize = 1000000000;
      var type = "";
      var res = await AppBace.request(
        url,
        "GET",
        {
          pageNum,
          pageSize
        },
        this.token
      );
      console.log(res);
      if (res.data.message === "success") {
        this.loading.close();
      } else {
        this.loading.close();
      }
      var array = res.data.data.rows;
      for (let index = 0; index < array.length; index++) {
        const element = array[index].taskId;
        // debugger
        if (element === e) {
          this.DataList1.push(array[index]);
          var inspact = array[index].type;

          var time = array[index].gmtFinish;
          // debugger
          switch (inspact) {
            case "inspect":
              this.DataList1[0].type = "例行巡查";
              // debugger
              break;
            case "repair":
              debugger;
              this.DataList1[0].type = "故障维修";

              break;
            case "maintenance":
              // debugger
              this.DataList1[0].type = "保养检修";

              break;
            case "mobile":
              // debugger
              this.DataList1[0].type = "移动手机";

              break;
          }
          this.DataList1[0].gmtFinish = AppBace.filterTime(time);
        }
        // if (time!==null) {
        //   this.DataList1[index].gmtFinish = AppBace.filterTime(time);
        // }else{
        //   this.DataList1[index].gmtFinish = "暂无"
        // }
      }
      // for (let index = 0; index < this.list.length; index++) {
      //   const element = this.list[index].gmtCreate;
      //   var inspact = this.list[index].type;
      //   switch (inspact) {
      //     case "inspect":
      //       this.list[index].type = "例行巡查";
      //       break;
      //     case "repair":
      //       this.list[index].type = "故障维修";
      //       break;
      //     case "maintenance":
      //       this.list[index].type = "保养检修";
      //       break;
      //   }
      //   this.list[index].gmtCreate = AppBace.filterTime(element);
      // }
    }
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    var id = this.$route.query.taskId;
    this.initData(id);
  }
};
</script>
<style scoped>
.contanerMain {
  margin: 0 5px;
  background: white;
  padding: 10px 20px;
  /* border-radius: 10px; */

  margin-top: 10px;
  /* margin-bottom: 50px; */
}
.eventDetailsDataList {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 40px;
  align-items: center;
  border-bottom: 1px solid rgb(238, 236, 236);
}
.header {
  padding-left: 10px;
}
.headerContent {
  margin-top: 10px;
}
</style>



